/* Grid Container 2 and all its elements */
.grid-container2 {
  border-color: brown;
  border-style: solid;;
  width:70%;
  height:auto;
  margin:auto;
  display: grid;
  grid-template-columns: 16.6% 16.6% 16.6% 16.6% 16.6% 16.6%;
  grid-template-rows: 100%;
  gap: 0px;
}

button {
  grid-row:1/2;
  grid-column:auto;
}